.navbar {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    padding: 1px 0;
    align-items: center;
    color: #ffffff;
    border-bottom: 4px solid #990f0200;
    box-shadow:  -3px -3px 10px #16161600, 3px 3px 10px #2c2c2c00;
    background-color: rgba(255, 255, 255, 0);
    transition: background-color 0.3s ease;
    z-index: 1000;
}

.navbar .logo img{
    width: 15%;
}

.nav_list{
    width: 90%;
    margin: 0 auto;
    padding: .5rem 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 4px solid #990f02;
    padding-bottom: .5rem;
    transition: all 0.3s ease;
}

.nav_list.nav_list-scrolled{
    border-bottom: 3px solid #990f0200;
    transform: translateY(5px);
}

.navbar.scrolled {
    background-color: #ffff;
    box-shadow:  -3px -3px 10px #16161641, 3px 3px 10px #2c2c2c49;
    border-bottom: 3px solid #990f02;
}

.nav-links li .links.text-scrolled{
    color: #cc5436d8;
}

h1 {
    margin: 0;
    font-size: 24px;
}

.nav-links {
    list-style: none;
    display: flex;
    gap: 20px;
}

.nav-links li {
    display: inline; /* Keep links inline */
}

.nav-links li .links {
    text-decoration: none;
    color: #ffffff;
    font-weight: 600;
    font-weight: lighter;
    transition: all 0.3s ease;
    font-family: sans-serif;
}

.nav-links li .links:hover {
    color: #1f1310;
    transform: translateY(5px);
}

/* Hamburger Icon Styles */
.hamburger {
    display: none; /* Hidden by default */
    flex-direction: column;
    cursor: pointer;
}

.bar {
    height: 3px;
    width: 25px;
    background-color: #990f0a;
    margin: 3px 0;
    transition: all 0.3s ease; 
}

/* Media Queries for Responsive Design */
@media (max-width: 768px) {
    .navbar {
       padding: 5px 0;
    }

    .nav-links {
        display: none;
        flex-direction: column;
        position: absolute;
        top: 100px;
        left: 50%;
        width: 100vw;
        right: 0;
        box-shadow: 2px 2px 10px #16161667;
        color: #ffff;
        background-color: #1a110f;
        padding: 40px 30px;
        border-radius: 5px;
    }

    .nav-links.active {
        display: flex; /* Show when active */
    }

    .hamburger {
        display: flex; 
    }
}
