.contact-button {
    position: fixed;
    bottom: 20px;
    left: 20px;
    background-color: #28a745;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    z-index: 1000;
    font-size: 16px;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s, opacity 0.3s;
    opacity: 0.9;
  }
  
.contact-button a{
  font-family: sans-serif;
}

  .contact-button:hover {
    background-color: #218838;
  }
  
  .contact-button:focus {
    outline: none;
  }
  