.contact_section hr{
    width: 7%;
    background: #990f0a;
    border: none;
    margin: 0 auto;
    padding: 3px 0;
}

.contact_section{
    overflow: hidden;
    width: 100vw;
    padding: 4rem 0;
}

.contact_section h1{
    text-align: center;
    padding: 2rem 0;
    font-size: 2rem;
}

.contact{
    background-color: #ffffff;
    box-shadow:  -3px -3px 6px #16161641, 3px 3px 6px #2c2c2c33;
    border-radius: 5px;
    padding: 3.5rem;
    gap: 2rem;
    width: 70%; 
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}

form{
    width: 50%;
}

.form-group{
    gap: 2rem;
    padding: .7rem 0;
    border-bottom: 2px sold #383838;
}

input[type="text"],
input[type="tel"],
input[type="email"],
textarea {
  width: 100%;
  padding: 1.5rem;
  font-size: 1rem;
  border-radius: 5px;
  border: none;
  border-bottom: 2px solid #990f0a;
  outline: none;
  background-color: whitesmoke;
  transition: background-color 0.3s ease;
}  

input[type="text"]:focus,
input[type="tel"]:focus,
input[type="email"]:focus,
textarea:focus {
  background-color: rgba(255, 255, 255, 0.89);
  outline: none;
}
  
textarea {
    resize: vertical;
} 

.form_text{
    width: 45%;
    display: grid;
    padding-left: 10rem;
    grid-template-columns: repeat(1, 1fr);
    align-items: center;
}

.text{
    display: flex;
    gap: 1rem;
}

.text img{
    width: 10%;
}

.text h3{
    font-size: 1.3rem;
}

.text p{
    font-size: 1rem;
}

@media (max-width: 768px){
    .contact{
        flex-direction: column;
    }
    form{
        width: 100%;
    }

input[type="text"],
input[type="tel"],
input[type="email"],
textarea {
  width: 80%;
  margin: 0 auto;
}

    .form_text{
        padding-top: 3rem;
        width: 70%;
        margin: 0 auto;       
        gap: 2rem;
        justify-content: center;
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 480px){
     .contact{
        width: 100%;
        padding: 1rem;
     }

     .contact_section h1{
        font-size: 1.4rem;
    }

    input[type="text"],
input[type="tel"],
input[type="email"],
textarea {
  width: 70%;
  margin: 0 auto;
}  

     .form_text{
        grid-template-columns: repeat(1, 1fr);
  
     }

     .text{
        flex-direction: row;
        position: relative;
        right: 9rem;
        align-items: center;

     }

     .text img{
        width: 13%;
     }
}