*{
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
 
}

body{
  overflow-x: hidden;
  scroll-behavior: smooth;
  font-family: sans-serif;
}

::-webkit-scrollbar{
  width: 7px;
}

::-webkit-scrollbar-thumb{
  background: #990f0a;
  border-radius: 20px;
}

h1{
  color: #28231d;
  font-family: "Baskervville";
  font-weight: 300;
}

h2{
  font-family: "Baskervville";
  font-weight: 300;
}

h3{
  font-family: "Baskervville";
  font-weight: 300;
}

h4{
  font-family: "Baskervville";
  font-weight: 300;
}

p{
  font-family: sans-serif;
  font-weight: 100;
}

span{
  font-family: "Montserrat";
  font-weight: 300;
}

li{
  list-style: none;

}

a{
  text-decoration: none;
}

.container{
  width: 90%; 
  margin: 0 auto;
}

.container_flex{
  width: 90%; 
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.reveal{
  position: relative;
  transform: translateY(150px);
  opacity: 0;
  transition: all 1s ease;
}

.reveal.active{
  transform: translateY(0px);
  opacity: 1;
}

.revealtwo{
  position: relative;
  transform: translateX(150px);
  opacity: 0;
  transition: all 1s ease;
}

.revealtwo.activetwo{
  transform: translateX(0px);
  opacity: 1;
}

.preloader{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: goldenrod;
  z-index: 10;
  display: grid;
  place-content: center;
  justify-items: center;
  transition: all 1s ease-in;
}

.preloader > * {
  transition: all 1s ease-in;
}

.preloader.loaded > *{
  opacity: 0;
} 

.preloader.loaded{
  transition-delay: 250ms;
  transform: translateY(100%);
}

.circle{
  width: 112px;
  height: 112px;
  border-radius: 100%;
  border: 3px solid #fff;
  border-block-start-color: black;
  margin-block-end: 55px;
 
  animation: rotate360 1s linear infinite;
}

@keyframes rotate360{
  0%{transform : rotate(0);}
  100% {transform: rotate(1turn);}
}

.preloader .text{
  background-size: 500%;
  font-size: calc(2rem + 3rem);
  font-weight: bolder;
  line-height: 1em;
  text-transform: uppercase;
  letter-spacing: 16px;
  padding-inline-start: 16px;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-stroke: 0.5px black;
  animation: loadingText linear 2s infinite; 
}

@keyframes loadingText{
  0%{
      background-position: 100%;
  }

  100%{
      background-position: 0%;
  }
}

.btn{
  padding: .8rem 2rem;
  border: none;
  font-size: 1.2rem;
  color: #f2f2f2;
  border-radius: 5px;
  background: #3b3131;
  box-shadow:  -3px -3px 5px #16161638, 3px 3px 5px #2c2c2c1e;
  transition: all 500ms ease;
}

.btn_red{
  padding: .8rem 2rem;
  border: none;
  font-size: 1.2rem;
  color: #ffffff;
  border-radius: 5px;
  background: #990f02;
  box-shadow:  -1px -1px 3px #16161638, 1px 1px 3px #2c2c2c1e;
  transition: all 500ms ease;
}

.btn:hover{
  transform: translateY(-5px);
}

@media (max-width: 768px){
  html{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
  }

  .container_flex{
    flex-direction: column;
  }

  h1{
    font-size: 1.5rem;
  }
}

@media (max-width: 480px){
  html{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden;
  }
}