@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-10px);
    }
    60% {
      transform: translateY(-5px);
    }
  }
  
  .scroll-down-button {
    position: fixed;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #990f02;
    color: white;
    border: none;
    z-index: 10;
    border-radius: 5px;
    padding: 10px 15px;
    font-size: 20px;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s, opacity 0.3s; /* Add opacity transition */
    animation: bounce 2s infinite; /* Add bounce animation */
    opacity: 1; /* Default opacity */
  }
  
  .scroll-down-button.fade-out {
    opacity: 0; /* Fade out when this class is added */
  }
  
  .scroll-down-button:hover {
    background-color: #2b1b17;
  }
  
  .intro {
    height: 100vh; /* Full height for demo */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #f8f9fa;
  }
  
  .target-section {
    min-height: 100vh; /* Ensures it is tall enough to scroll */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #e9ecef;
  }
  