/* src/FAQ.css */
.faq-container {
    width: 100%;
    margin: 0 auto;
    background-color: #2b1b17;
    font-family: 'Arial', sans-serif;
    padding: 4rem 0;
}
 
.faq-container h2 {
    font-size: 2.5rem;
    text-align: center;
    margin-bottom: 20px;
    color: #ffffff;
}

.faq-list {
    border-radius: 8px;
    width: 90%;
    display: flex;
    justify-content: center;
    display: grid;
    grid-template-columns: repeat(2, 40%);
    gap: 2rem;
    margin: 0 auto;
}

.faq-item:last-child {
    border-bottom: none;
}

.faq-question {
    padding: 2.5rem 2rem;
    cursor: pointer;
    border-radius: 5px;        
    background-color: #ffffff;
    margin: 0 auto;
    border-top: 2px solid #990f0a;
    align-items: center;
    box-shadow:  -3px -3px 10px #16161641, 3px 3px 10px #2c2c2c33;
    transition: background-color 0.3s ease;
}

.faq-question:hover {
    background-color: #f1f1f1;
}

.faq-answer{ 
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.3s ease, padding 0.3s ease;
     color: #555;
}

.faq-answer.show {
    max-height: 200px; /* A value large enough for the answer */
    padding: 1.5rem 1rem; /* Add padding when shown */
    background-color: #fafafa;
}

.arrow {
    margin-left: 10px;
    color: #990f0a;
    transition: transform 0.3s ease;
}

.arrow {
    transform: rotate(0deg);
}

.arrow.show {
    transform: rotate(180deg);
}

@media (max-width: 768px){
    .faq-list{
        width: 100%;
    }
}

@media (max-width: 480px){
    .faq-container{
        padding: 3rem 1rem;
        margin: 0 auto;
    }

   .faq-container h2{
    font-size: 1.5rem;
   }
     
   .faq-list {
    grid-template-columns: repeat(1, 90%);
    justify-content: center;
    position: relative;
    right: 1rem;
    font-size: .9rem;
  }

}
