/* Footer.module.css */
.footer {
    background: #2b1b17;
    color: #ffffff;
    text-align: center;
    font-family: 'Helvetica Neue', sans-serif;
  }

  .company_logo{
    width: 100%;
    background-color: #fff;
    padding: .5rem 0;    
  }
  
  .company_logo img{
    width: 20%;
    margin: 0 auto;
  }
  

  .containers {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
    padding: 4rem 0;
    width: 90%;
    margin: 0 auto;
  }
  
  .abouts p{
    font-style: italic;
    font-size: 1rem;
  }

  .serv ul li{
    list-style: none;
    font-size: 1rem;
    padding: 1.5rem 0;
  }

  footer h3 {
    margin-bottom: 10px;
    font-size: 1.5em;
    padding-bottom: 5px;
  }
  

  .links a {
    color: #ecf0f1;
    text-decoration: none;
    font-size: 1rem;
    padding: .5rem 1rem;
    margin: 5px 0;
    transition: color 0.3s, transform 0.3s;

  }
  
  .links a:hover {
    color: #ffdd57;
    transform: translateY(-2px);
  }

  .contacts_logo{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
  }
  
  .contacts .contacts_logo img{
    width: 20%;
  }

  .footer hr{
    border: none;
    background-color: #990f0a;
    width: 90%;
    margin: 0 auto;
    padding: 1px 0;
  }

  .testimonial {
    margin-top: 20px;
    font-style: italic;
    font-size: 0.9em;
    opacity: 0.8;
  }
  

  
  .copy {
    margin-top: 20px;
    font-size: 0.8em;
    opacity: 0.7;
  }
  
  /* Responsive styles */
  @media (max-width: 768px) {
    .containers {
     grid-template-columns: repeat(2, 1fr);
    }
  }

  @media (max-width: 480px){
    .containers{
      grid-template-columns: repeat(1, 90%);
      align-items: center;
      justify-content: center;
      text-align: center;
    }

    .links a {
      color: #ecf0f1;
      text-decoration: none;
      font-size: .8rem;
      padding: .5rem .5rem;
      margin: 5px 0;
      transition: color 0.3s, transform 0.3s;
  
    }
  }
  