header{
    overflow: hidden;
    width: 100vw;
    height: 140vh;
}

header::before{
    content: '';
    width: 100%;
    height: 115%;
    position: absolute;
    top: 0;
    right: 0;
    background-image: url('../images/homeimg.jpg');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    z-index: -1;
}

.header_h1{
    font-size: 4rem;
    font-weight: 1;
}

.content{
    width: 50%;
    padding-top: 12%;
}

@keyframes bounced {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-10px);
    }
    60% {
      transform: translateY(-5px);
    }
  }


.meet_lawyers{
    text-decoration: none;
    color: #fff;
    animation: bounced 2s infinite;
    background-color: #990f02;
    transition: all 500ms ease;
    border-radius: 5rem;
    padding: .5rem 1rem;
    box-shadow:  -3px -3px 10px #16161650, 3px 3px 10px #2c2c2c38;
}

.content a:hover{
    transform: translateY(-5px);
}

.content h1{
    font-size: 3rem;
    padding: 2rem 0;
    color: #fff;
}

.content p{
    color: #fff;
    font-size: 1.3rem;
    padding-bottom: 1.5rem;
}

.header_boxes{
    width: 90%;
    background-color: #fff;
    position: relative;
    top: 10%;
    padding: 1rem 0;
    box-shadow: 3px 3px 10px #808080af;
    border-radius: 2px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin: 0 auto;
    z-index: 10;
    box-shadow:  -3px -3px 10px #16161650, 3px 3px 10px #2c2c2c38;
}

.box{
    text-align: center;
    padding: 4rem 1.5rem;
    border-right: 1px solid #8080808a;
    transition: all 500ms ease;
    border-bottom: 3px solid #990f0200;
}

.box:hover{
    border-left: 3px solid #990f02;
    transform: translateY(-7px);
}

.box h2{
    color: #990f02;
    font-size: 1.3rem;
    padding: 1rem 0;
}

.box img{
    width: 15%;
    padding-bottom: .8rem;
    margin: 0 auto;
}

@media (max-width: 768px) {
 
    .content{
        width: 100%;
        margin-top: 7rem;
  }

    .header_boxes{
        grid-template-columns: repeat(2, 1fr);
    }

    .content h1{
        font-size: 2rem;
    }
}

@media (max-width: 480px){
    header{
        height: 200vh;
    }

    .content{
        text-align: center;
        margin-top: 35%;
    }

    .header_boxes{
        position: relative;
        z-index: 1;
        grid-template-columns: repeat(1, 1fr);
        top: 8%;
        padding: 2.5rem 0;
    }
    .box{
        padding: 3rem 1rem;
    }
}