.articles{
    padding: 5rem 0;
}

.article_hr{
    width: 7%;
    background: #990f0a;
    border: none;
    padding: 3px 0;
}

.articles h1{
    font-size: 3rem;
}

.article_text{
    width: 70%;
    padding: 1.5rem 0;
}

.articles_container{
    display: grid;
    grid-template-columns: repeat(4, 23%);
    gap: 2rem;
    align-items: center;
    margin: 0 auto;
    padding: 3rem 0;
}

.article{
    box-shadow:  -3px -1px 5px #16161633, 3px 1px 5px #2c2c2c11;
    margin: 0 auto;
    border-bottom: 3px solid #990f0a;
}

.article img{
    width: 100%;
    border-radius: 5px;
}

.article_content{
    background-color: #fff;
    padding: 1rem;
}

.article_content h2{
    color: #990f0a;
    font-size: 1.3rem;
    padding: .6rem 0;
}

.article_content img{
    width: 40%;
}

.article_links{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .3rem 0;
}

.article_links a{
    text-decoration: none;
    color: #fff;
    padding: .5rem .5rem;
    font-weight: 100;
    font-size: .8rem;
    border-radius: 10px;
    background-color: #990f0a;
}

@media (max-width: 768px){
    .articles_container{
      grid-template-columns: repeat(2, 45%);
      justify-content: center;
    }
}

@media (max-width: 480px){
    .articles h1{
        font-size: 1.5rem;
    } 
    
    .article_text{
        width: 100%;
        padding: 1.5rem 0;
    }
    
    .articles_container{
        display: flex;
        justify-content: center;
        flex-direction: column;
        width: 90%;
    }
    
}