.about{
   width: 100vw;
   padding: 7rem 0;
}

.about_text{ 
    width: 44%;
    gap: 3rem;
    padding-top: 5.5rem;
}  

.about_box img{
    width: 20%;
}

.hr{
    border: none;
    border-radius: 5%;
    background-color: #990f02;
    height: 1%;
    width: 20%;
}

.about_text h1{
    padding: .8rem 0;
    font-size: 2.5rem;
}
.about_text h3{
    padding: .8rem 0;
    color: #990f02;
}
.about_text p{
    padding: .8rem 0;
    font-size: 1.2rem;
}

.about_boxes{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
    padding-top: 1.5rem;
}

.about_box{
    text-align: center;
}

.about_images{
    width: 45%;
    height: 100vh;
    display: flex;
    padding-top: 8rem;
    justify-content: flex-end;
}

.about_image1{
    width: 30%;
    height: 30%;
    border-radius: 5%;
    background-color: #2b1b17;
    position: relative;
    bottom: 5rem;
    /*right: 15rem;*/
}

.about_image2{
    background-image: url(../images/home.jpg);
    width: 50vh;
    background-position: center;
    background-size: cover;
    height: 70vh;
    border-radius: 2%;
    position: absolute;
}

.about_image3{
    background-image: url(../images/aboutimg.jpg);
    width: 45%;
    height: 45%;
    border-radius: 2%;
    border: 10px solid #fff;
    background-position: center;
    background-size: cover;
    position: relative;
    top: 17rem;
    right: 15rem;
    z-index: 1;
}

@media (max-width: 768px){
    .about{
        padding: 0;
    }
    
    .about_text{
        width: 90%;
    }
    .about_images{
        width: 90%;
    }

    .about_image1{
        width: 50%;
        height: 30%;
    }

    .about_image3{
        top: 25rem;
        right: 20rem;
    }
}

@media (max-width: 480px){
     .about{
        padding: 0;
     }

      .about_text{
        width: 100%;
        text-align: center;
    }

    .about_text h1{
        font-size: 2rem;
    }
   
    .about_text p{
      font-size: 1rem;
    }
    .about_boxes{
        grid-template-columns: repeat(2, 1fr);
    }

    .about_box{
        padding: 0;
    }

    .about_images{
        width: 100%;
    }

    .about_image1{
        width: 60%;
        height: 20%;
        border-radius: 5%;
        background-color: #990f02;
        position: relative;
        bottom: 5rem;
        /*right: 15rem;*/
    }
    
    .about_image2{
        width: 35vh;
        height: 55vh;
        border-radius: 2%;
        position: absolute;
    }
    
    .about_image3{
        width: 50%;
        height: 25%;
        position: relative;
        top: 20rem;
        right: 12rem;
        z-index: 1;
    }
}