.services{
  width: 100%;
  text-align: center; 
  padding: 1.6rem 0;
}

.services h2{
    padding-bottom: 1rem;
}

.services h1{
    font-size: 2.5rem;
}

.services hr{
    border: none;
    width: 10%;
    margin: 0 auto;
    background-color: #990f02;
    height: 5px;
}

.service_p{
    text-align: center;
}

.service_boxes{
    display: grid;
    width: 90%;
    padding: 3rem 0;
    gap: 2rem;
    margin: 1rem auto;
    grid-template-columns: repeat(4, 1fr);
}

.service_box{
    border-radius: 5px;
    padding: 3.5rem 1rem;
    text-align: center;
    border-bottom: 3px solid #990f0200;
    box-shadow:  -3px -3px 6px #16161623, 3px 3px 6px #2c2c2c31;
    transition: all 500ms ease;
}

.service_box img{
    width: 15%;
    margin: 0 auto;
    padding: 1rem 0;
}

.service_box h3{
    color: #7c2b17;
    padding: 1rem 0;
}

.box_sec{
    padding: 3.5rem 1rem;
    background-color: #2b1b17;
    border-radius: 5px;
    color: #ffffff;
    text-align: center;
    border-bottom: 2px solid #990f0200;
    box-shadow:  -3px -3px 10px #16161641, 3px 3px 10px #2c2c2c49;
    transition: all 500ms ease;
}

.box_sec:hover{
    transform: translateY(-7px);
   border-bottom: 2px solid #990f02;
}

.box_sec h3{
    color: #ffffff;
}

.service_box:hover{
   transform: translateY(-7px);
   border-bottom: 2px solid #990f02;
   box-shadow:  -3px -3px 10px #16161638, 3px 3px 10px #2c2c2c48;
}

@media (max-width: 768px){
   
 .services h1{
  font-size: 2.5rem;
}

.service_box1{
    width: 30%;
}

.service_box1 p{
    font-size: 1rem;
    top: 20%;
}

.service_box1 img{
    width: 100%;
    top: 50%;
}
.service_box3{
    width: 30%;
}
 
.service_box3 p{
    font-size: 1rem;
    top: 30%;
}

.service_box3 img{
    width: 35%;
    top: 15%;
}

.service_boxes{
    grid-template-columns: repeat(2, 1fr);
}

}

@media (max-width: 480px){

    .service1{
        flex-direction: column;
        justify-content: center;
        padding: 3rem 0;
        text-align: center;
        width: 95%;
    }

    .service_p{
        padding: 1rem 0;
    }

    .services h1{
        font-size: 1.6rem;
        color: #2b1b17;
      }

      .service_boxes{
        padding-top: 2rem;
      }
      
      .service_box1{
          width: 90%;
          display: flex;
          justify-content: space-between;
      }
      
      .service_box1 p{
          font-size: 1rem;
          top: 0;
          width: 45%;
      }
      
      .service_box1 img{
          width: 45%;
          top: 0;
    }

      .service_box3 p{
        width: 45%;
        top: 20%;
      }

      .service_box2 img{
       display: none;
    }
      .service_box3{
          width: 90%;
          display: flex;
          justify-content: space-between;
      }
       
      .service_box3 p{
          font-size: 1rem;
          width: 50%;
          top: 15%;
      }
      
      .service_box3 img{
          width: 35%;
          top: 0;
      }

      .service_boxes{
        grid-template-columns: repeat(1, 1fr);
        padding: 1rem 0;
        gap: 2rem;
    }
    
    .service_box{
        width: 90%;
        text-align: center;
     }

    .service_box p{
        font-size: 1.1rem;
    }
      
}