@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
      transform: translateY(0);
    }
    40% {
      transform: translateY(-10px);
    }
    60% {
      transform: translateY(-5px);
    }
  }

.scroll-to-top-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    transform: translateX(-50%);
    background-color: #990f02;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    font-size: 20px;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s, opacity 0.3s; /* Add opacity transition */
    animation: bounce 2s infinite; /* Add bounce animation */
    opacity: 1; /* Default opacity */
    transform: translateX(-50%);
    background-color: #990f02;
    z-index: 1000;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    font-size: 20px;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s, opacity 0.3s; /* Add opacity transition */
    animation: bounce 2s infinite; /* Add bounce animation */
    opacity: 1; /* Default opacity */
    transform: translateX(-50%);
    background-color: #990f02;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px 15px;
    font-size: 20px;
    cursor: pointer;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: background-color 0.3s, opacity 0.3s; /* Add opacity transition */
    animation: bounce 2s infinite;
    opacity: 1; /* Default opacity */
  }
  
  .scroll-to-top-button:hover {
    background-color: #2b1b17;
  }
  
  .scroll-to-top-button:focus {
    outline: none;
  }
  
  .scroll-to-top-button.fade-out {
    opacity: 0; /* Fade out effect */
  }
  