.cases_section{
    padding: 2rem 1rem;
    background-color: #2b1b17;
    color: #fff;
    margin-top: 1rem;
}

.case_container{
    border-radius: 2px;
    padding: 1.5rem;
}

.case_content{
    width: 60%;
    padding: 1.5rem 0;
}
.case_content hr{
    border: none;
    width: 10%;
    background-color: #990f0a;
    height: 5px;
}

.case_content h1{
    padding: .7rem 0;
    font-size: 2.5rem;
    color: #fff;
}

.cases_content h2{
   color: #7c2b17;
   padding: .5rem 0;
}

.cases_content h4{
    padding: .5rem 0;
}

.cases_content span{
    font-weight: 100;
    font-style: italic;
}

.cases{
    display: flex;
    width: 100%;
    margin: 1rem auto;
    padding: 1.5rem 2rem;
    border-top: 2px solid #fff;
}

.case{
    text-align: center;
    transition: all 500ms ease;
    padding: 1rem 0;
}

.case:hover{
    box-shadow:  -3px -3px 10px #16161600, 3px 3px 10px #2c2c2c00;
}

.cases_content{
    padding-bottom: 2rem;
}

.case a{
    margin-top: 1.5rem;
}

.case h2{
    font-size: 1rem;
}

@media (max-width: 768px){
    .case_container{
        padding: 4rem 0;
    }

    .case_content{
        width: 90%;
    }

    .case_container{
        padding: 0;
    }

    .case{
        padding: 0;
    }
}

@media (max-width: 480px){
    .case{
        padding: 2rem 0;
    }


    .cases_section{
        padding: 3rem 0;
    }
    
    .case_container{
        padding: 0;
    }
    .case_content{
        width: 100%;
    }
}