.lawyers{
    padding: 2rem 0;
}

.lawyers h1{
    text-align: center;
    font-size: 2.5rem;
    padding: 1rem 0;
}

.main_hr{
    width: 8%;
    border: none;
    padding: 2px 0;
    margin: 0 auto;
    background-color: #990f02;
}

.lawyers h2{
    padding: 3rem 0;
}

.btn_red-slim{
    font-weight: 100;
    font-size: .9rem;
    color: #7c2b17;
}

.attorney{
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    margin: 1rem auto;
}

.lawyer6{
   position: relative;
   bottom: 13rem;
   right: 2rem;
}

.img-cover{
    background-color: #2b1b17;
    height: 40vh;
}

.attorney_img{
    width: 35%;
}

.attorney_imgs{
    display: flex;
    justify-content: center;
    width: 25%;
    flex-direction: column;
    gap: 1rem;
}

.img_cover{
    width: 100%;
    background-color: #2b1b17;
    height: 35vh;
    border-radius: 5px;
}

.img_name{
    border-radius: 5px;
    padding: .5rem;
    text-align: center;
    box-shadow:  -3px -3px 10px #16161633, 3px 3px 10px #2c2c2c11;
}

.img_name hr{
    background-color: #990f02;
    width: 60%;
    margin: 0 auto;
    border: none;
    height: 2px;
}

.img_name h4{
    font-family: sans-serif;
    color: #2b1b17;
}

.img_name p{
    font-style: italic;
    padding: .5rem 0;
    font-size: .8rem;
}



.attorney_text{
    width: 60%;
}

.attorney_text h2{
    font-size: 2rem;
    padding: 1.5rem 0;
}

.attorney_text hr{
    padding: 1px 0;
    background-color: #990f02;
    border: none;
}

.attorney_p{
    padding: 1.5rem 0;
}

.attorney_post{
    font-size: 1.2rem;
}

.experiences{
    padding: .6rem 0;
    display: grid;
    width: 70%;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
}

.contact_logo{
    width: 20%; 
    display: flex;
    justify-content: space-between;
    padding: 1rem 0;
}

.contact_logo img{
    width: 50%;
}

.contact_logo-other{
    width: 100%; 
    display: flex;
    margin: 0 auto;
    justify-content: space-between;
    padding: 1rem 1rem;
    align-items: center;
}

.contact_logo-other div{
    width: 50%;
    display: flex;
    justify-content: space-between;
}

.contact_logo-other img{
    width: 25%;
}

.other_h2{
    text-align: center;
}

.other_attorneys{
    display: flex;
    width: 100%;
    margin: 1rem auto;
}

.firm_lawyer{
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    box-shadow: 1px 1px 5px #00000075;
    justify-content: center;
    padding: 2rem .5rem 0 .5rem;
}

.firm_lawyer .img{
    width: 60%;
    background-color: #2b1b17;
    border-radius: 5px;
    margin: 0 auto;
}

.lawyer_info{
    padding: 2rem 0;
    gap: 1rem;
    display: flex;
    flex-direction: column;
}

.lawyer_details{
   display: flex;
   width: 100%;
   align-items: center;
   justify-content: space-between;
}

.lawyer_details h3{
    font-size: 1.1rem;
}

.lawyer_contact{
    display: flex;
    gap: .5rem;
    width: 30%;
}

.lawyer_contact img{
    width: 40%;
}

.other_info{
    display: flex;
    justify-content: space-between;
}

.slick-slide > div{
    margin: .5rem 2rem;
}


@media (max-width: 768px){
    .attorney{
        flex-direction: column;
        width: 90%;
    }
    .attorney_img{
        width: 60%;
    }

    .attorney_text{
        width: 90%;
    }    

    .lawyer6{
        width: 170%;
        position: relative;
        bottom: 68%;
        right: 45%;
    }
}

@media (max-width: 480px){
    .attorney{
        width: 100%;
    }

    .lawyers h1{
        font-size: 1.5rem;
        width: 70%;
        margin: 0 auto;
    }

    .lawyers h2{
        text-align: center;
    }

    .attorney_img{
        width: 80%;
    }

    .attorney_text{
        width: 100%;
    }

    .lawyer6{
        width: 170%;
        position: relative;
        bottom: 57%;
        right: 45%;
    }
}